//  Vue  Vuex
import Vue from "vue"
import Vuex from 'vuex'
import moduleCart from './cart/cart'
import moduleUser from './user/user'

// VuexVue
Vue.use(Vuex)

// Store
export default new Vuex.Store({
    modules: {
        'm_cart': moduleCart,
        'm_user': moduleUser
    }
})

